import axios from "axios";
import { useEffect, useState } from "react";
import playButton from "../img/play.png";

const IMG_API = "https://image.tmdb.org/t/p/w1280";



const IndividualMovie = (props, id) => {
    const [movie, setMovie ] = useState("");

    const setVoteClass = (vote) => {
        if(vote >= 8) {
            return "green";
        }
        else if(vote >= 6) {
            return "orange";
        } else {
            return "red";
        }
    }
    
    useEffect(() => {
        axios
            .get(`https://api.themoviedb.org/3/movie/${props.id}?api_key=e0eb34f3bddc89aabdffd507b370db1e&language=en-US`)
            .then((res) => { setMovie(res.data)
                console.log(res.data);
            })
            .catch((err) => {
                console.log(err);
            })
    }, []);

    const bodyStyle = {
        backgroundImage: `url(${IMG_API + movie.backdrop_path})`,
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat",
        color: "white",
        position: "relative",
    }
    
    const dateConverter = require("./Functions");
    // // console.log(movie.genres[1])
    // console.log(movie.genres.map());
    
    return (
        <>
            <div className="individual-movie-container" style={bodyStyle}>
                <div className="color-overlay"></div>
                <div className="solo-movie-container">
                    <img src={IMG_API + movie.poster_path} />
                </div>
                <div className="individual-movie-info">
                        <h1 className="movie-h1">{movie.title}</h1>
                        <div className="genres">
                            <p>{movie.release_date} • </p>
                            {/*<a>• {movie.genres[0].name}, </a>
                            <a>{movie.genres[1].name} •</a>*/}
                            <p>{movie.runtime} mins</p> 
                        </div>
                        <div className="trailer-button">
                            <span className={
                                `tag ${setVoteClass(movie.vote_average)}`}>
                                {movie.vote_average}
                            </span>
                            <img src={playButton}/>
                            <a href={`/TMDB-Project/movies/videos/${movie.id}`}>Videos</a>
                        </div>
                        <h4><i>{movie.tagline}</i></h4>
                        <h3>Overview</h3>
                        <p>{movie.overview}</p>
                    </div>
                </div>
        </>    
    )
}
export default IndividualMovie;